import axios from 'axios'
import React, { useEffect, useState } from 'react'
import CardHolder from './CardHolder'
import './css/CardsContainer.css'
import appendScript from '../../libs/importScript'
// import ScriptTag from 'react-script-tag'

function CardsContainer(){
    const [projects, setProjects] = useState([])


    useEffect(() => {

        var options = {
            headers: {'secret-key': '$2b$10$EEfsGSaGNPVMfRmz1.zuP.G02OKjb3jv1JWwxXia.bus9E6CTal8m'}
        }

        axios.get('https://api.jsonbin.io/b/6002df93e31fbc3bdef45f66/4', options)
            .then((res) => {
                setProjects(res.data)

                appendScript('/scripts/cardAnimation.js')
            })
            .then(() => {
                console.log('data loaded')
            })

    }, [])

    return (
        <div>
            <h1 className='showcase-project-title'>Showcased Projects</h1>
            <div className='cardContainer'>
                <CardHolder projects={projects.slice(0, projects.length / 2)} />
                <CardHolder projects={projects.slice(projects.length / 2)} />
            </div>

            {/* <ScriptTag type='text/javascript' src='/scripts/cardAnimation.js' /> */}
        </div>
    )
}

export default CardsContainer